import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';


import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';
import { PerfilService } from '../../../services/perfil/perfil.service';
import { Perfil } from '@app/viewmodel/perfil';
import { NotificationService, LocalDataService } from '@app/shared';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  
  item: Perfil;
  submit = false;
  @ViewChild('ctrlform') ctrlform: any;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private perfilservice: PerfilService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
  ) {
    this.candeactivateguard.setedit(true);
  }


  ngOnInit() {
    this.item = new Perfil();
    this. getById() ;
  }
  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.perfilservice.getById($id)
      .subscribe((response) => {
        this.item = response.data;
        this.spinnerService.hide();
      })
  }

  add() {
    this.submit = true;
    if (this.ctrlform.invalid) {
      return false;
    }
    this.submit = false;
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;

    this.perfilservice.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Perfil de usuario actualizado.');
        this.router.navigateByUrl('perfiles');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Perfil de usuario ya existe");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }


}



