import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../../shared';
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";
import { PerfilService } from '../../../services/perfil/perfil.service';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {
  listaitem = [];
  table = null;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private perfilService: PerfilService,
    private notificationService: NotificationService) { }

  ngOnInit() {
    this.spinnerService.show();

    this.get();

  }
  get() {

    this.perfilService.get(1)
      .subscribe((response) => {
        this.listaitem = response.data;
        this.reInitDatatable();
        this.spinnerService.hide();

      });
  }
  loadtable() {
    let exampleId: any = $('#example23');

    this.table = exampleId.DataTable({
      dom: 'Bfrtip',
      buttons: [
        'copy', 'csv', 'excel', 'pdf', 'print'
      ],
      oLanguage: {
        "sLengthMenu": "_MENU_ registros por página",
        "sInfoFiltered": " - filtrada de _MAX_ registros",
        "sSearch": "Buscar: ",
        "sZeroRecords": "No existen datos diponibles",
        "sInfoEmpty": " ",
        "sInfo": 'Mostrando _END_ de _TOTAL_',
        "oPaginate": {
          "sPrevious": "Anterior",
          "sNext": "Siguiente"
        }
      }
    });
  }
  ngAfterViewInit() {



  }
  delete(id) {
    this.spinnerService.show();
    this.perfilService.delete(id)
      .subscribe((value) => {
        this.notificationService.onSuccess('Perfil de usuario  Eliminada con Exito.')
        this.get();
        this.spinnerService.hide();
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 400) {
          this.notificationService.onError("El perfil no se puede eliminar ya que tiene informacion asociada.");
        } else {
          this.notificationService.onError("Se ha producido un error.");
        }
      });
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy()
      this.table = null
    }
    setTimeout(() => this.loadtable(), 0)
  }


}



