import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './index/index.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';

const routes: Routes = [ { path: '', component: IndexComponent},
{ path: 'add', component: AddComponent, canDeactivate: [Candeactivateguard]},
{ path: 'edit/:id', component: EditComponent, canDeactivate: [Candeactivateguard]}];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PerfilRoutingModule { }



